@font-face {
  font-family: "Roboto Condensed";
  font-weight: normal;
  font-display: swap;
  src: local("Roboto Condensed"), url("../fonts/RobotoCondensed-Regular.woff2") format("woff2"), url("../fonts/RobotoCondensed-Regular.woff") format("woff"); }

@font-face {
  font-family: "Roboto Condensed";
  font-weight: bold;
  font-display: swap;
  src: local("Roboto Condensed"), url("../fonts/RobotoCondensed-Bold.woff2") format("woff2"), url("../fonts/RobotoCondensed-Bold.woff") format("woff"); }

@font-face {
  font-family: "Roboto Condensed";
  font-weight: lighter;
  font-display: swap;
  src: local("Roboto Condensed"), url("../fonts/RobotoCondensed-Light.woff2") format("woff2"), url("../fonts/RobotoCondensed-Light.woff") format("woff"); }

@font-face {
  font-family: "Bebas Neue";
  font-weight: normal;
  font-display: swap;
  src: local("Bebas Neue"), url("../fonts/BebasNeue-Regular.woff2") format("woff2"), url("../fonts/BebasNeue-Regular.woff") format("woff"); }

@font-face {
  font-family: "Bebas Neue";
  font-weight: bold;
  font-display: swap;
  src: local("Bebas Neue"), url("../fonts/BebasNeue-Bold.woff2") format("woff2"), url("../fonts/BebasNeue-Bold.woff") format("woff"); }

html {
  box-sizing: border-box; }

*,
*::before,
*::after {
  box-sizing: inherit; }

body {
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-size: 16px;
  font-weight: normal;
  line-height: 150%;
  color: #102654; }

a {
  text-decoration: none; }

img {
  max-width: 100%;
  height: auto;
  vertical-align: middle; }

h2 {
  font-family: "Bebas Neue", "Arial", sans-serif;
  font-weight: bold;
  font-size: 49px;
  color: #102654; }

/* tablet */
@media (max-width: 1199px) {
  h2 {
    font-size: 42px; } }

/* mobile */
@media (max-width: 767px) {
  h2 {
    font-size: 32px; } }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

.container {
  max-width: 1250px;
  margin: 0 auto;
  padding: 0 45px; }

/* mobile */
@media (max-width: 767px) {
  .container {
    padding-right: 15px;
    padding-left: 15px; } }

.contacts {
  padding: 70px 45px; }

.contacts h2 {
  margin: 0 0 58px 0; }

.contacts__dt {
  font-weight: bold;
  margin-bottom: 15px; }

.contacts__dd {
  margin: 0;
  font-size: 14px;
  line-height: 175%; }

/* tablet */
@media (max-width: 1199px) {
  .contacts h2 {
    margin: 0 0 50px 0; }
  .contacts__dt {
    font-size: 20px;
    margin-bottom: 20px; }
  .contacts__dd {
    margin: 0;
    font-size: 18px;
    line-height: 152%; } }

/* mobile */
.footer__top {
  display: flex;
  border-top: 1px solid #74819C; }

.footer__form {
  background-color: #102654; }

.footer__bottom {
  padding-top: 44px;
  padding-bottom: 44px;
  background-color: #030A1A; }

.footer__info {
  display: flex;
  justify-content: space-between;
  align-items: center; }

.footer__menu,
.footer__social {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex; }

.footer__social {
  align-items: center; }

.footer__list {
  margin-right: 40px; }
  .footer__list:last-child {
    margin-right: 0; }

.footer__link {
  font-size: 14px;
  color: #FFFFFF; }

/* tablet */
@media (max-width: 1199px) {
  .footer__top {
    border-top: none; }
  .footer__bottom {
    padding-top: 47px;
    padding-bottom: 47px; }
  .footer__list {
    margin-right: 30px; } }

/* mobile */
@media (max-width: 767px) {
  .footer__top {
    display: block; }
  .footer__bottom {
    padding-top: 50px;
    padding-bottom: 50px; }
  .footer__info {
    display: block; }
  .footer__link {
    font-size: 16px; } }

.form {
  padding: 70px 45px; }

.form h2 {
  color: #FFFFFF;
  margin: 0 0 58px 0; }

input {
  width: calc(50% - 15px);
  padding-bottom: 10px;
  margin-bottom: 30px;
  background-color: transparent;
  font-size: 14px;
  color: #FFFFFF;
  border: none;
  border-bottom: 1px solid #74819C; }
  input::placeholder {
    color: #FFFFFF;
    font-size: 14px; }
  input:hover, input:focus {
    box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25); }

.form__fields {
  display: flex;
  justify-content: space-between; }

/* tablet */
@media (max-width: 1199px) {
  .form h2 {
    margin: 0 0 50px 0; }
  input {
    width: calc(50% - 30px); } }

/* mobile */
@media (max-width: 767px) {
  input {
    width: 100%;
    margin-bottom: 40px; }
  .form__fields {
    display: inline; } }

.header {
  background-color: red;
  padding-top: 30px; }

.header__wrapper {
  display: flex;
  justify-content: space-between; }

.header__phone {
  color: #FFFFFF;
  font-family: "Bebas Neue", "Arial", sans-serif;
  font-size: 26px;
  font-weight: bold;
  letter-spacing: 3%; }

/* tablet */
@media (max-width: 1199px) {
  .header {
    padding-top: 40px; }
  .header__phone {
    font-size: 24px;
    letter-spacing: 2%; } }

/* mobile */
@media (max-width: 767px) {
  .header {
    padding-top: 20px; }
  .header__phone {
    font-size: 20px; } }

.reviews {
  padding-bottom: 120px;
  padding-top: 120px; }

.reviews h2 {
  margin: 0 0 62px 0;
  text-align: center; }

.review {
  display: flex;
  margin-right: auto;
  margin-left: auto;
  max-width: 560px;
  padding: 62px;
  background-color: #1C3374; }

.review__img,
.review__text {
  margin: 0;
  color: #FFFFFF; }

.review__text {
  max-width: 328px; }

.review__img {
  margin-right: 28px; }

.review span {
  font-weight: bold;
  font-size: 20px;
  margin-bottom: 25px; }

.review p {
  line-height: 140%; }

/* tablet */
@media (max-width: 1199px) {
  .reviews {
    padding-bottom: 100px; }
  .reviews h2 {
    margin: 0 0 50px 0; }
  .review {
    padding-left: 65px;
    padding-right: 65px;
    max-width: 566px; } }

/* mobile */
@media (max-width: 767px) {
  .reviews {
    padding-bottom: 70px; }
  .review {
    padding: 40px 25px;
    max-width: 226px; }
  .review__img {
    width: 68px; } }

.social__item {
  margin-right: 25px; }
  .social__item:last-child {
    margin-right: 0; }

.social__link {
  display: block; }

/* tablet */
@media (max-width: 1199px) {
  .social__item {
    margin-right: 20px; } }

/* mobile */
@media (max-width: 767px) {
  .social__item {
    margin-right: 35px; } }

.btn {
  padding: 0;
  border: none;
  font: inherit;
  color: inherit;
  font-weight: bold;
  font-size: 12px;
  letter-spacing: 5%;
  border: 4px solid #FFFFFF;
  text-transform: uppercase;
  background-color: transparent;
  cursor: pointer; }

.btn__form-sub {
  width: 100%;
  min-height: 52px;
  padding: 20px 165px;
  color: #FFFFFF;
  font-size: 14px; }
  .btn__form-sub:hover, .btn__form-sub:focus {
    background-color: #0c1c3f; }

